import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Box,
  AppBar,
  Toolbar,
  Typography,
  Drawer,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  IconButton,
  Card,
  CardContent,
  Grid,
  Badge,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Avatar,
} from '@mui/material';
import {
  Menu as MenuIcon,
  Dashboard as DashboardIcon,
  Apps as AppsIcon,
  CheckCircle as CheckCircleIcon,
  Pending as PendingIcon,
  Logout as LogoutIcon,
} from '@mui/icons-material';
import { useAuth } from '../contexts/AuthContext';
import axios from 'axios';

interface Stats {
  totalApps: number;
  unconfirmedApps: number;
  confirmedApps: number;
  pendingReviews: number;
}

const Dashboard: React.FC = () => {
  const [stats, setStats] = useState<Stats>({
    totalApps: 0,
    unconfirmedApps: 0,
    confirmedApps: 0,
    pendingReviews: 0,
  });
  const [drawerOpen, setDrawerOpen] = useState(false);
  const { user, logout, token } = useAuth();
  const navigate = useNavigate();
  const [leaderboard, setLeaderboard] = useState<{ name: string; email: string; count: number; userId: string }[]>([]);

  useEffect(() => {
    const fetchStats = async () => {
      try {
        console.log('Fetching stats with token:', token);
        const response = await axios.get('http://localhost:3001/api/stats', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        console.log('Stats response:', response.data);
        setStats(response.data);
      } catch (error) {
        console.error('Error fetching stats:', error);
      }
    };

    const fetchLeaderboard = async () => {
      try {
        const response = await axios.get('http://localhost:3001/api/leaderboard', {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        setLeaderboard(response.data);
      } catch (error) {
        console.error('Error fetching leaderboard:', error);
      }
    };

    if (token) {
      fetchStats();
      fetchLeaderboard();
    }
  }, [token]);

  const handleNavigation = (path: string) => {
    navigate(path);
    setDrawerOpen(false);
  };

  const handleLogout = () => {
    logout();
    navigate('/login');
  };

  const menuItems = [
    { text: 'Dashboard', icon: <DashboardIcon />, path: '/' },
    { text: 'All Apps', icon: <AppsIcon />, path: '/apps' },
    {
      text: 'Cluster Verification',
      icon: (
        <Badge badgeContent={stats.unconfirmedApps} color="error">
          <PendingIcon />
        </Badge>
      ),
      path: '/unconfirmed',
    },
    {
      text: 'Confirmed Apps',
      icon: <CheckCircleIcon />,
      path: '/confirmed',
    },
  ];

  return (
    <Box sx={{ display: 'flex' }}>
      <AppBar position="fixed">
        <Toolbar>
          <IconButton
            color="inherit"
            edge="start"
            onClick={() => setDrawerOpen(true)}
            sx={{ mr: 2 }}
          >
            <MenuIcon />
          </IconButton>
          <Typography variant="h6" noWrap component="div" sx={{ flexGrow: 1 }}>
            LP App Name Deduplicator
          </Typography>
          <Typography variant="body1" sx={{ mr: 2 }}>
            {user?.name}
          </Typography>
          <IconButton color="inherit" onClick={handleLogout}>
            <LogoutIcon />
          </IconButton>
        </Toolbar>
      </AppBar>

      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <Box sx={{ width: 250 }}>
          <List>
            {menuItems.map((item) => (
              <ListItem
                button
                key={item.text}
                onClick={() => handleNavigation(item.path)}
              >
                <ListItemIcon>{item.icon}</ListItemIcon>
                <ListItemText primary={item.text} />
              </ListItem>
            ))}
          </List>
        </Box>
      </Drawer>

      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          mt: 8,
          backgroundColor: 'background.default',
          minHeight: '100vh',
        }}
      >
        <Grid container spacing={3}>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Total Apps
                </Typography>
                <Typography variant="h4">{stats.totalApps}</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Unconfirmed Apps
                </Typography>
                <Typography variant="h4">{stats.unconfirmedApps}</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Confirmed Apps
                </Typography>
                <Typography variant="h4">{stats.confirmedApps}</Typography>
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} sm={6} md={3}>
            <Card>
              <CardContent>
                <Typography color="textSecondary" gutterBottom>
                  Pending Reviews
                </Typography>
                <Typography variant="h4">{stats.pendingReviews}</Typography>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
        {/* Leaderboard */}
        <Card sx={{ mt: 4, maxWidth: 480 }}>
          <CardContent>
            <Typography variant="h6" gutterBottom color="text.primary" sx={{ fontWeight: 700 }}>
              Leaderboard
            </Typography>
            <TableContainer component={Paper} sx={{ borderRadius: 2, boxShadow: 2 }}>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ fontWeight: 600 }}>#</TableCell>
                    <TableCell sx={{ fontWeight: 600 }}>User</TableCell>
                    <TableCell sx={{ fontWeight: 600 }}>Confirmed Apps</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {leaderboard.map((row, idx) => (
                    <TableRow key={row.userId}>
                      <TableCell>{idx + 1}</TableCell>
                      <TableCell>
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                          <Avatar sx={{ width: 28, height: 28, bgcolor: 'primary.main' }}>{row.name[0]}</Avatar>
                          <Typography variant="body2" color="text.primary">{row.name}</Typography>
                        </Box>
                      </TableCell>
                      <TableCell>
                        <Typography variant="body2" color="text.primary">{row.count}</Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                  {leaderboard.length === 0 && (
                    <TableRow>
                      <TableCell colSpan={3} align="center">
                        <Typography variant="body2" color="text.secondary">No confirmed apps yet.</Typography>
                      </TableCell>
                    </TableRow>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </CardContent>
        </Card>
      </Box>
    </Box>
  );
};

export default Dashboard; 